import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Category from "../views/Category.vue";
import Category1 from "../views/Category1.vue";
import Cars from "../views/Cars.vue";
import My from "../views/My.vue";
import Bootpage from "../views/Bootpage.vue";
import Banner from "../views/Banner.vue";
import Test from "../views/Test.vue";
import Test1 from "../views/Test1.vue";
import Test2 from "../views/Test2.vue";
import Test3 from "../views/Test3.vue";
import Test4 from "../views/Test4.vue";
import Goods from "../views/Goods.vue";
import Login from "../views/Login.vue";
import Order from "../views/Order.vue";
import Settlement from "../views/Settlement.vue";
import Register from "../views/Register.vue";
import address from "../views/address.vue";
import Collection from "../views/Collection.vue";
import Regiment from "../views/Regiment.vue";
import After from "../views/After.vue";
import Account from "../views/Account.vue";
import SettleAccounts from "../views/SettleAccounts.vue";


Vue.use(VueRouter);

const routes = [
  { path: "/", name: "", component: Bootpage },
  { path: "/category", name: "", component: Category },
  { path: "/category1", name: "", component: Category1 },
  { path: "/cars", name: "", component: Cars },
  { path: "/my", name: "", component: My },
  { path: "/Home", name: "", component: Home },
  { path: "/banner", name: "", component: Banner },
  { path: "/test", name: "", component: Test },
  { path: "/test1", name: "1", component: Test1 },
  { path: "/test2", name: "2", component: Test2 },
  { path: "/test3", name: "3", component: Test3 },
  { path: "/test4", name: "4", component: Test4 },
  { path: "/goods", name: "", component: Goods },
  { path: "/login", name: "", component: Login },
  { path: "/order", name: "", component: Order },
  { path: "/settlement", name: "", component: Settlement },
  { path: "/register", name: "", component: Register },
  { path: "/address", name: "", component: address },
  { path: "/collection", name: "", component: Collection },
  { path: "/regiment", name: "", component: Regiment },
  { path: "/after", name: "", component: After },
  { path: "/account", name: "", component: Account },
  { path: "/settleAccounts", name: "", component: SettleAccounts },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
