<template>
  <div>
    <van-button class="roundbtn" icon="arrow-left" type="default" hairline round @click="pathgo" />

    <div class="goods">
      <van-swipe class="goods-swipe" :autoplay="3000">
        <van-swipe-item v-for="thumb in goods.thumb" :key="thumb">
          <img :src="thumb" />
        </van-swipe-item>
      </van-swipe>

      <van-cell-group>
        <van-cell>
          <div class="goods-title">{{ goods.title }}</div>
          <div class="goods-price">{{ formatPrice(goods.price) }}</div>
        </van-cell>
        <van-cell class="goods-express">
          <van-col span="10">{{ goods.express }}</van-col>
          <van-col span="14">{{ goods.remain }}</van-col>
        </van-cell>
      </van-cell-group>

      <van-cell-group class="goods-cell-group">
        <van-cell value="" icon="shop-o" is-link @click="sorry">
          <template slot="title">
            <span class="van-cell-text"></span>
            <van-tag class="goods-tag" type="danger"></van-tag>
          </template>
        </van-cell>
        <van-cell title="" icon="location-o" is-link @click="sorry" />
      </van-cell-group>

      <van-cell-group class="goods-cell-group">
        <van-cell title="" is-link @click="sorry" />
      </van-cell-group>

      <!--  -->
      <van-tabs type="card">
        <van-tab title="" title-active-color="#fff">
          <div class="box">
            <span v-for="twxq in goods.twxq" :key="twxq">
              <img :src="twxq" alt />
            </span>
          </div>
        </van-tab>
        <van-tab title="" title-active-color="#fff">
          <div class="box-test">
            <span class="txt1"></span>
            <span class="txt2"></span>
          </div>
          <div class="box-test">
            <span class="txt1"></span>
            <span class="txt3">()</span>
          </div>
        </van-tab>
        <van-tab title="" title-active-color="#fff" class="box-txt">
          <span class="txt bt"></span>
          <span class="txt wb"> 7 </span>
          <span class="txt bt">?</span>
          <span
            class="txt wb"
          >1-2cm~</span>
          <span class="txt bt"></span>
          <span class="txt wb">~</span>
          <span class="txt bt"></span>
          <span class="txt wb">()</span>
          <span class="txt bt"></span>
          <span class="txt wb">77</span>
          <span class="txt bt"></span>
          <span class="txt wb">~</span>
          <span class="txt bt"></span>
          <span class="txt wb">1.65</span>
          <span
            class="txt wb"
          >2.</span>
          <span
            class="txt wb"
          >4. </span>
          <span
            class="txt wb"
          >4. </span>
        </van-tab>
      </van-tabs>

      <van-goods-action>
        <van-goods-action-icon icon="chat-o" @click="sorry"></van-goods-action-icon>
        <van-goods-action-icon icon="cart-o" @click="onClickCart"></van-goods-action-icon>
        <van-goods-action-button type="warning" @click="addCar"></van-goods-action-button>
        <van-goods-action-button type="danger" @click="sorry"></van-goods-action-button>
      </van-goods-action>
    </div>
  </div>
</template>

