<style lang="scss">
.van-button--hairline.roundbtn {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.5);
  color: #000;
}
.goods {
  padding-bottom: 50px;
  &-swipe {
    img {
      width: 100%;
      display: block;
    }
  }
  &-title {
    font-size: 16px;
  }
  &-price {
    color: #f44;
  }
  &-express {
    color: #999;
    font-size: 12px;
    padding: 5px 15px;
  }
  &-cell-group {
    margin: 15px 0;
    .van-cell__value {
      color: #999;
    }
  }
  &-tag {
    margin-left: 5px;
  }
}
.box {
  span {
    width: 100%;
    img {
      width: 100%;
      display: block;
    }
  }
}
.box-txt {
  padding: 20px;
  .txt {
    display: block;
    text-align: left;
  }
  .wb {
    color: #9b9b9b;
  }
  .bt {
    color: #4a4a4a;
    padding-bottom: 5px;
  }
}
.txt1 {
  width: 80px;
  padding: 0 20px;
  display: block;
  text-align: left;
  float: left;
  color: #9b9b9b;
}
.txt2 {
  display: block;
  float: left;
}
.txt3 {
  display: block;
  float: left;
  color: rgb(255, 119, 0);
}
.box-test {
  width: 100%;
  overflow: hidden;
  padding: 10px 0;
}
.van-tab--active {
  color: #ee0a24;
}
</style>


